<template>
  <div class="write-info">
    <navbar text="填写资料"></navbar>

    <scroller>
			<template v-if="!next">
				<form action="" class="form">
					<div class="form-item">
						<label for="" class="form-item__label">姓名</label>
						<input v-model="name" type="text" class="form-item__input" placeholder="请输入姓名">
					</div>
					<div class="form-item">
						<label for="" class="form-item__label">性别</label>
							<van-radio-group v-model="sex">
								<van-radio name="1" checked-color="#ff0204">男</van-radio>
								<van-radio name="2" checked-color="#ff0204">女</van-radio>
							</van-radio-group>
						</van-radio-group>
					</div>
					
					<div class="form-item" @click="openPopup('datePopup')">
						<label for="" class="form-item__label">出生年月</label>
						<input v-model="date" disabled type="text" class="form-item__input" placeholder="请输入出生年月">
					</div>
					<div class="form-item" @click="openPopup('cityPopup')">
						<label for="" class="form-item__label">省市区</label>
						<input v-model="address" disabled type="text" class="form-item__input" placeholder="请输入省市区">
					</div>
					<div class="form-item">
						<label for="" class="form-item__label">详细地址</label>
						<input v-model="addressInfo" type="text" class="form-item__input" placeholder="请输入详细地址">
					</div>
				</form>
				<button @click="done" class="u-button u-button--primary u-button--big">完成</button>
			</template>
			
			<template v-else>
				<form action="" class="form">
					<div class="form-item">
						<input v-model="iphone" type="number" class="form-item__input" placeholder="手机号">
					</div>
					<div class="form-item form-item_verify">
						<input v-model="verifyCode" type="text" class="form-item__input" placeholder="输入验证码">
						<button class="form-item__btn" :class="{disable: isGetVerifyCode}" @click="getVerifyCode">获取验证码</button>
					</div>
				</form>
				<button class="u-button u-button--primary u-button--big " :class="{disable: !isGetVerifyCode}">下一步</button>
			</template>
    </scroller>
		<van-popup v-model="datePopup" position="bottom">
			<van-datetime-picker
				v-model="currentDate"
				type="date"
				@change="setDate"
				@confirm="closePopup('datePopup')"
			/>
		</van-popup>
		<van-popup v-model="cityPopup" position="bottom">
			<van-area :area-list="areaList" @confirm="setCity" />
		</van-popup>
		
		
  </div>
</template>

<script>
import { post, host, data } from "@/utils";
import navbar from "@/components/navbar";
export default {
  data() {
    return {
      datas: [],
			next: false,
			name: '',
			sex: 1,
			date: '',
			address: '',
			addressInfo: '',
			currentDate: new Date(),
			datePopup: false,
			cityPopup: false,
			areaList: data,
			
			iphone: null,
			verifyCode: '',
			isGetVerifyCode: false
    };
  },
  components: {
    navbar
  },
  computed: {
    
  },

  methods: {
    // 打开date
		openPopup(popup) {
			this[popup] = true
		},
		closePopup (popup) {
			this[popup] = false
		},
		// 设置日期
		setDate (e) {
			console.log(e)
			let checkedDate = e.getValues()
			this.date = `${checkedDate[0]}-${checkedDate[1]}-${checkedDate[2]}`
		},
		setCity (e) {
			this.address = `${e[0].name},${e[1].name},${e[2].name}`
			this.cityPopup = false
		},
		done() {
			this.next = true
		},
		getVerifyCode () {
			this.isGetVerifyCode = true
		}
		
  },

  created() {
    
  }
};
</script>
<style lang="scss" scoped>
.van-radio-group {
	display: flex;
	/deep/ .van-radio__input {height: 2em}
	.van-radio {margin-right: 30px;}
	.van-radio__label {margin-left: 5px;}
}
</style>
